body {
  padding: 0;
  margin: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  background-color: white;
  box-sizing: border-box;
  overflow-x: hidden; }

a {
  text-decoration: none; }

*,
*::before,
*::after {
  box-sizing: inherit; }

.button {
  width: 280px;
  height: 40px;
  color: white;
  background-color: #68b738;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: center; }

.container {
  display: flex;
  width: 500px;
  padding-left: 20px;
  padding-right: 20px;
  margin-right: auto;
  margin-left: auto; }

/*
 Index - главная страница
 */
.page-header a {
  color: black; }

.page-header ul {
  list-style-type: none; }

.page-header {
  width: 320px;
  display: flex;
  margin-right: auto;
  margin-left: auto;
  justify-content: center; }

.page-header__wrapper {
  display: flex;
  width: 280px; }

.page-header-top {
  display: flex;
  width: 100%; }

.main-nav__button {
  margin-left: auto; }

.advantages {
  padding-bottom: 20px; }

.advantages__title {
  font-size: 36px;
  font-weight: 400; }

.advantages__list {
  list-style: none;
  margin: 0;
  padding: 0; }

.advanages__item {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  padding-bottom: 20px;
  padding-left: 80px; }

.advanages__item:last-child {
  padding-bottom: 0; }

.banner__title {
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  color: #ffffff;
  text-align: center; }

.banner__text {
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center; }

.banner {
  margin-bottom: 20px; }

.banner__picture {
  text-align: center; }

.exemplar {
  background-color: #eeeeee;
  width: 320px;
  display: flex;
  flex-wrap: wrap;
  margin-right: auto;
  margin-left: auto;
  justify-content: center; }

.exemplar__wrapper {
  width: 280px; }

.exemplar__title {
  font-size: 36px;
  font-weight: 400;
  line-height: 60px;
  margin: 0;
  padding-top: 25px;
  padding-bottom: 25px; }

.exemplar__info {
  font-size: 14px;
  font-weight: 400;
  font-family: "Arial", sans-serif;
  padding-bottom: 25px; }

.result__title {
  font-size: 24px;
  font-weight: 400;
  margin: 0;
  padding-bottom: 15px; }

.result__info {
  font-size: 14px;
  font-weight: 400;
  font-size: 12px;
  line-height: 12px; }

.exemplar__result {
  display: flex;
  justify-content: space-between; }

.result__time,
.result__weight {
  display: flex;
  flex-direction: column;
  align-items: center; }

.exemplar__expenses {
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center; }

.exemplar__after {
  display: flex;
  justify-content: space-around;
  text-transform: uppercase;
  padding-bottom: 35px; }

.find-us {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 30px; }

.find-us__wrapper {
  display: flex;
  align-items: center;
  width: 280px; }

.find-us__invite {
  font-size: 16px;
  font-weight: 400;
  padding-bottom: 25px;
  padding-top: 25px;
  line-height: 20px; }

.find-us__adress {
  font-size: 14px;
  font-weight: 400;
  padding-bottom: 25px;
  padding-top: 25px;
  font-family: "Arial", sans-serif;
  line-height: 20px;
  font-style: normal; }

.page-footer {
  background-color: #eeeeee;
  display: flex;
  width: 320px;
  justify-content: center;
  margin-right: auto;
  margin-left: auto; }

.page-footer__logo {
  display: flex;
  justify-content: center;
  padding-top: 35px;
  padding-bottom: 35px; }

.page-footer__wrapper {
  width: 280px; }

.page-footer__social {
  display: flex;
  width: 110px;
  justify-content: space-between;
  padding: 0;
  margin: 0;
  padding-top: 25px;
  padding-bottom: 25px;
  margin-left: auto;
  margin-right: auto;
  list-style: none; }
  .page-footer__social a {
    color: black; }

.page-footer__copyright {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 25px; }

.page-main {
  width: 320px;
  display: flex;
  flex-wrap: wrap;
  margin-right: auto;
  margin-left: auto;
  justify-content: center; }

.page-main a {
  color: black; }

.page-main__wrapper {
  width: 280px; }

.page-main__background {
  background-image: url("/img/index-background-mobile@1x.jpg");
  background-repeat: no-repeat;
  width: 100%; }

.page-main__title {
  font-size: 36px;
  font-weight: 400;
  line-height: 60px; }

.page-main__product-list {
  list-style: none; }

.type-feed__title {
  font-size: 24px;
  line-height: 37px;
  font-weight: 400;
  text-transform: uppercase;
  text-align: center; }

.type-feed__text {
  font-size: 14px;
  font-weight: 400;
  font-family: "Arial", sans-serif;
  padding-left: 20px;
  padding-right: 20px; }

.type-feed__link-catalog {
  text-transform: uppercase;
  font-size: 16px;
  line-height: 16px;
  font-weight: 400;
  padding-left: 20px; }

.type-feed__los-weight,
.type-feed__mass-gain {
  background-color: #eeeeee;
  padding-bottom: 8px;
  padding-top: 8px;
  margin-bottom: 20px; }

.page-main__product-list a {
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  color: white; }

.product-list h3 {
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase; }

.product-list table {
  font-size: 12px;
  font-weight: 400;
  line-height: 16px; }

.product-list__more h3 {
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase; }

.product-list__more span {
  font-size: 12px;
  font-weight: 400;
  line-height: 16px; }

.product-list__more a {
  color: black;
  background-color: #eeeeee; }
