@mixin title-top {
  font-size: 36px;
  font-weight: 400;
}

@mixin title-middle {
  font-size: 24px;
  font-weight: 400;
}

@mixin title-small {
  font-size: 16px;
  font-weight: 400;
}

@mixin text-all {
  font-size: 14px;
  font-weight: 400;
}

$green: #68b738;
$gray: #eeeeee;
$color-main: $green;
$color-back: $gray;

$font-one: "Oswald", "Arial", sans-serif;
